let that;
import {
	dateFormat
} from '@/utils/util.js'
export default {
	data() {
		return {
			current: '',
			videoContext: '',
			old: '',
			arrData: [],
			imgLength: '',
			scrollTop: 0,
			delId: '',
			isDel: false,
		}
	},
	props: {
		list: {
			type: Array
		},
		isvideo: {
			type: Boolean
		},
		scrollHeight: {
			type: Number
		}
	},
	watch: {
		list(a, b) {
			
			var arr = a.map(o=> Object.assign({},o))
			a.forEach((el, i) => {
				if (a.length - 1 != i) {
					if (dateFormat(+el.code.split('@')[1]) == dateFormat(+a[i + 1].code.split('@')[1])) {
						arr[i + 1].code = ''
					}
				}
			})
			that.arrData = arr
			// if(b.length) {
			// 	arr.forEach((el,i)=> {
			// 		b.forEach((item,index)=>{
			// 			if(item.id == el.id && item.imgList.length < el.imgList.length) {
			// 				el.imgList.push({})
			// 				console.log(el.imgList.length)
			// 				el.imgList.splice(el.imgList.length - 1 , 1)
			// 				console.log(el.imgList.length)
							
			// 			}
			// 		})
			// 	})
			// }
			// console.log(that.arrData)
			
		}
	},
	created() {
		that = this
	},
	methods: {
		edit: function(item) {
			this.$emit('edit', item)
		},
		longtap: function(item) {
			this.isDel = !this.isDel
			this.delId = item.id
		},
		del: function(item) {
			this.$emit('del', item)
		},
		ChooseImage: function(list, url) {
			var arr = []
			list.forEach((el, i) => {
				arr.push(el.url)
			})
			uni.previewImage({
				urls: arr,
				current: url
			});
		},
		play: function(id) {
			if (this.current) {
				this.videoContext.pause()
			}
			if (this.current == id) {
				this.videoContext.pause()
			} else {

				this.current = id
				this.videoContext = uni.createVideoContext('myVideo' + this.current, this)
				this.videoContext.play()
			}
		},
		lower: function() {
			this.$emit('lower')
		},
		scroll:  function(e) {
			if(that.scrollTop < e.detail.scrollTop) {
				this.$emit('isScroll',false)
			} else {
				this.$emit('isScroll',true)
			}
			this.scrollTop = e.detail.scrollTop
		}
	},
	filters: {
		timeFormat: function(arg, type) {
			if(!arg) {
				return '';
			}
			var time = arg.split('@')

			if (dateFormat(+time[1]) == dateFormat(new Date().getTime())) {
				if (type == 'day') {
					return '今'
				} else {
					return '天'
				}
			} else {

				var date = new Date(+time[1])
				var m = date.getMonth() + 1 + '月'
				var d = date.getDate()

				if (type == 'day') {
					return d
				} else {
					return m
				}
			}
		}
	}
}
